@font-face {
    font-family: 'noto_serifregular';
    src: url('../assets/fonts/notoserif-regular-webfont.woff2') format('woff2'),
    url('../assets/fonts/notoserif-regular-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body, html {
    height: 100%;
    min-height: 100%;
    padding: 0;
    margin: 0;
}

#root {
    height: 100%;
    min-width: 100%;
}

.wrapper {
    height: 100%;
}

.centered {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.centered div {
    font-family: noto_serifregular;
    text-align: center;
    color: #fff;
}

.name {
    font-size: 80px;
}

.gender {
    font-size: 30px;
}

.region {
    font-size: 30px;
}

.squaredFour {
    width: 20px;
    position: relative;
    margin: 20px auto;
}

.squaredFour label {
    width: 20px;
    height: 20px;
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    background: #fcfff4;
    background: -webkit-gradient(linear, left bottom, left top, from(#fcfff4), color-stop(40%, #dfe5d7), to(#b3bead));
    background: -webkit-linear-gradient(bottom, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
    background: -o-linear-gradient(bottom, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
    background: linear-gradient(to top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
    border-radius: 4px;
    -webkit-box-shadow: inset 0px 1px 1px white, 0px 1px 3px rgba(0, 0, 0, 0.5);
            box-shadow: inset 0px 1px 1px white, 0px 1px 3px rgba(0, 0, 0, 0.5);
}

.squaredFour label:after {
     content: '';
     width: 9px;
     height: 5px;
     position: absolute;
     top: 4px;
     left: 4px;
     border: 3px solid #333;
     border-top: none;
     border-right: none;
     background: transparent;
     opacity: 0;
     -webkit-transform: rotate(-45deg);
         -ms-transform: rotate(-45deg);
             transform: rotate(-45deg);
 }

.squaredFour label:hover::after {
     opacity: 0.5;
 }

.squaredFour input[type=checkbox] {
    visibility: hidden;
}
.squaredFour input[type=checkbox]:checked + label:after {
     opacity: 1;
 }


